import React from 'react'
import Image from '../assets/profil.jpg'

export const Home = () => {
  return (
    <div className='h-[400px] lg:h-[800px]'>
    <div className='section mx-auto' id='home'>
        <div className="container pt-[100px] lg:pt-[270px] lg:pl-[250px] mx-auto">
            <div className='flex flex-col gap-y-8 lg:flex-row lg:gap-x-12 items-center'>  
                <div className='h-40 w-40'>
                    <img src={Image} alt='' className='rounded-xl'/>
                </div>
                <div className='flex-1 text-center lg:text-left'>
                    <div className='mb-[-6px] text-[25px] lg:text-[50px] font-semibold uppercase leading-[1]'>
                        <p className='text-black'>
                            Hi Saya
                        </p>
                    </div>
                    <h1 className='uppercase text-[25px] lg:text-[50px] font-bold leading[0.8]'>
                        Aryo Bagas Sasongko
                    </h1>
                </div>
            </div>
        </div>
    </div>
    </div>
  )
}
